    getNextAgent: function(next_id, list) {
      var that = this;
      /* ,   ,  ,     */
      var nextEl = document.getElementById(next_id);
      var nextElUpperText = nextEl.textContent;
      var nextElText = nextElUpperText.toLowerCase();
      var targetInst = list.indexOf(nextElText);
      var currentNewIndex = targetInst;
      var nextIndex = targetInst + 1;
      var prevIndex = targetInst - 1;
      if (nextIndex === list.length || nextIndex === 0) {
        nextIndex = 0;
        currentNewIndex = list.length - 1;
        prevIndex = list.length - 2;
      } else if (nextIndex === 1) {
        currentNewIndex = 0;
        prevIndex = list.length - 1;
      }
      /*    html */

      if (list === that.b2_uniqueItemsList) {
        that.showCurrentIndex = currentNewIndex;
        that.showPrevIndex = prevIndex;
        that.showNextIndex = nextIndex;
        that.institution = list[currentNewIndex];
      }

      if (list === that.b3_uniqueItemsList) {
        that.showCurrentIndex_b3 = currentNewIndex;
        that.showPrevIndex_b3 = prevIndex;
        that.showNextIndex_b3 = nextIndex;
        that.sphere = list[currentNewIndex];
      }

      if (list === that.b4_uniqueItemsList) {
        that.showCurrentIndex_b4 = currentNewIndex;
        that.showPrevIndex_b4 = prevIndex;
        that.showNextIndex_b4 = nextIndex;
        that.structure = list[currentNewIndex];
      }
    },
    getPrevtAgent: function(prev_id, list) {
      var that = this;
      /* ,    */
      var prevEl = document.getElementById(prev_id);
      var prevElText = prevEl.textContent;
      prevElText = prevElText.toLowerCase();
      var targetInst = list.indexOf(prevElText);
      var prevIndex = targetInst - 1;
      var currentNewIndex = targetInst;
      var nextIndex = targetInst + 1;

      if (prevIndex === -1) {
        prevIndex = list.length - 1;
      } else if (prevIndex === list.length - 2) {
        nextIndex = 0;
      }
      /*   */
      if (list === that.b2_uniqueItemsList) {
        that.showCurrentIndex = currentNewIndex;
        that.showPrevIndex = prevIndex;
        that.showNextIndex = nextIndex;
        that.institution = list[currentNewIndex];
      }

      if (list === that.b3_uniqueItemsList) {
        that.showCurrentIndex_b3 = currentNewIndex;
        that.showPrevIndex_b3 = prevIndex;
        that.showNextIndex_b3 = nextIndex;
        that.sphere = list[currentNewIndex];
      }

      if (list === that.b4_uniqueItemsList) {
        that.showCurrentIndex_b4 = currentNewIndex;
        that.showPrevIndex_b4 = prevIndex;
        that.showNextIndex_b4 = nextIndex;
        that.structure = list[currentNewIndex];
      }
    },
    redrawB1Map: function() {
      var that = this;
      d3.selectAll("#b1_map svg path").on("click", function() {
        let region = d3.select(this).attr("id");
        let regionUkrName = d3.select(this).attr("value");
        that.region = region;
        that.regionUkrName = regionUkrName;
        that.redrawChart(that.b1_radar_region, "b1_radar_r");
      });
    },
    // 
    drawRadar: function(df_radar, container, path_id, colorScale) {
      var that = this;
      var angleSlice = (Math.PI * 2) / df_radar[0].length;
      var radarLine = d3
        .radialLine()
        .curve(d3.curveCardinalClosed)
        .radius(function(d) {
          return that.rScale(d.value);
        })
        .angle(function(d, i) {
          return i * angleSlice;
        });

      var svg = d3
        .select(container)
        .attr(
          "width",
          that.cfg.w + that.cfg.margin.left + that.cfg.margin.right
        )
        .attr(
          "height",
          that.cfg.h + that.cfg.margin.top + that.cfg.margin.bottom
        )
        .attr("class", "radar");

      var g = svg
        .append("g")
        .attr(
          "transform",
          "translate(" +
            (that.cfg.w / 2 + that.cfg.margin.left) +
            "," +
            (that.cfg.h / 2 + that.cfg.margin.top) +
            ")"
        )
        .on("mouseover", function() {
          if (
            (container === "#b1_ukr_radarChartSvg") |
            (container === "#b1_reg_radarChartSvg")
          ) {
            d3.selectAll(".b1_radar_U").style("display", "block");
            d3.selectAll(".b1_radar_r").style("display", "block");
          } else {
            d3.select(this.parentNode)
              .selectAll(".axisWrapper text")
              .style("display", "block");
          }
        })
        .on("mouseout", function() {
          d3.selectAll(".axisWrapper text").style("display", "none");
        });
      //Filter for the outside glow
      /*  var filter = g
          .append("defs")
          .append("filter")
          .attr("id", "glow"); */
      /* feGaussianBlur = filter
          .append("feGaussianBlur")
          .attr("stdDeviation", "2.5")
          .attr("result", "coloredBlur"),
        feMerge = filter.append("feMerge"), */
      /* feMergeNode_1 = feMerge.append("feMergeNode").attr("in", "coloredBlur"), */
      /*  feMergeNode_2 = feMerge
          .append("feMergeNode")
          .attr("in", "SourceGraphic"); */

      //Wrapper for the grid & axes
      var axisGrid = g.append("g").attr("class", "axisWrapper");

      // //Draw the background circles
      axisGrid
        .selectAll(".levels")
        .data(d3.range(1, that.cfg.levels + 1).reverse())
        .enter()
        .append("circle")
        .attr("class", "gridCircle")
        .attr("r", function(d) {
          return (that.radius / that.cfg.levels) * d;
        })
        .style("fill", "none")
        .style("stroke", "#CDCDCD")
        .style("stroke-opacity", that.cfg.opacityCircles)
        .style("filter", "url(#glow)");

      //Create the straight lines radiating outward from the center
      var axis = axisGrid
        .selectAll(".axis")
        .data(df_radar[0])
        .enter()
        .append("g")
        .attr("class", function(d) {
          let axisColor = that.color_alternative_b2(d.axis);
          return "axis " + path_id + " " + axisColor;
        });

      //Append the lines
      axis
        .append("line")
        .attr("x1", 0)
        .attr("y1", 0)
        .attr("x2", function(d, i) {
          return (
            that.rScale(that.maxValue * 1.1) *
            Math.cos(angleSlice * i - Math.PI / 2)
          );
        })
        .attr("y2", function(d, i) {
          return (
            that.rScale(that.maxValue * 1.1) *
            Math.sin(angleSlice * i - Math.PI / 2)
          );
        })
        .attr("class", "line")
        .style("stroke", "grey")
        .style("stroke-width", "1px");

      axis
        .append("text")
        .attr("class", "legend " + path_id)
        .style("font-size", "11px")
        .attr("text-anchor", "middle")
        .attr("dy", "0")
        .attr("x", function(d, i) {
          return (
            that.rScale(that.maxValue * that.cfg.labelFactor + 0.2) *
            Math.cos(angleSlice * i - Math.PI / 2)
          );
        })
        .attr("y", function(d, i) {
          return (
            that.rScale(that.maxValue * that.cfg.labelFactor) *
            Math.sin(angleSlice * i - Math.PI / 2)
          );
        })
        .text(function(d) {
          return Math.round(d.value * 100) + "%";
        })
        .attr("fill", function(d) {
          return colorScale(d.axis);
        });

      //Create a wrapper for the blobs
      var blobWrapper = g
        .selectAll(".radarWrapper")
        .data(df_radar)
        .enter()
        .append("g")
        .attr("class", "radarWrapper");

      //Append the backgrounds
      blobWrapper
        .append("path")
        .attr("id", path_id)
        .attr("class", "radarArea")
        .attr("d", function(d) {
          return radarLine(d);
        })
        .attr("fill", "grey")
        .style("fill-opacity", that.cfg.opacityArea);

      blobWrapper
        .selectAll(".axisLabel")
        .data(d3.range(1, that.cfg.levels + 1).reverse())
        .enter()
        .append("text")
        .attr("class", "axisLabel")
        .attr("x", 10)
        .attr("y", function(d) {
          return (-d * that.radius) / that.cfg.levels;
        })
        .attr("dy", "0.4em")
        .style("font-size", "10px")
        .attr("fill", "#737373")
        .text(function(d) {
          var theVal = (that.maxValue * d) / that.cfg.levels;
          return that.Format(theVal);
        });

      axis
        .append("circle")
        .attr("class", function(d) {
          return "circle " + path_id + " " + d.axis;
        })
        .attr("r", 7)
        .attr("dy", "-1em")
        .attr("cx", function(d, i) {
          return that.rScale(d.value) * Math.cos(angleSlice * i - Math.PI / 2);
        })
        .attr("cy", function(d, i) {
          return that.rScale(d.value) * Math.sin(angleSlice * i - Math.PI / 2);
        })
        .attr("fill", function(d) {
          return colorScale(d.axis);
        })
        .style("cursor", "pointer")
        .on("mouseover", function(d) {
          let elClass = d3
            .select(this)
            .attr("class")
            .split(" ")[2];
          d3.selectAll("." + elClass)
            .attr("r", 10)
            .style("color", colorScale(d.axis))
            .style("font-weight", "bold");
        })
        .on("mouseout", function() {
          let elClass = d3
            .select(this)
            .attr("class")
            .split(" ")[2];
          d3.selectAll("." + elClass)
            .attr("r", 7)
            .style("color", "inherit")
            .style("font-weight", "normal");
        });
    },

    // 
    redrawChart: function(df_radar, path_id) {
      var that = this;
      var angleSlice = (Math.PI * 2) / df_radar[0].length;
      const radarLine = d3
        .radialLine()
        .curve(d3.curveCardinalClosed)
        .radius(function(d) {
          return that.rScale(d.value);
        })
        .angle(function(d, i) {
          return i * angleSlice;
        });

      d3.select("path#" + path_id + ".radarArea")
        .data(df_radar)
        .transition()
        .duration(750)
        .attr("d", function(d) {
          return radarLine(d);
        });

      d3.selectAll("text.legend." + path_id)
        .data(df_radar[0])
        .transition()
        .duration(750)
        .text(function(d) {
          return Math.round(d.value * 100) + "%";
        });

      if (
        that.institution === " " ||
        that.institution === " " ||
        that.institution === " " ||
        that.institution === "  "
      ) {
        d3.select(".axis.b2_radar_path.red").style("display", "none");
      } else {
        d3.select(".axis.b2_radar_path.red").style("display", "block");
      }

      if (that.structure === "" || that.structure === "") {
        d3.select(".axis.b4_radar_path.red").style("display", "none");
      } else {
        d3.select(".axis.b4_radar_path.red").style("display", "block");
      }

      d3.selectAll(".circle." + path_id)
        .data(df_radar[0])
        .transition()
        .duration(750)
        .attr("cx", function(d, i) {
          return that.rScale(d.value) * Math.cos(angleSlice * i - Math.PI / 2);
        })
        .attr("cy", function(d, i) {
          return that.rScale(d.value) * Math.sin(angleSlice * i - Math.PI / 2);
        });
    },

    line1_path: function() {
      var path = this.d3LinePath(this.b2_line1_data.data);
      return path;
    },
    area1_path: function() {
      var path = this.d3AreaPath(this.b2_line1_data.data);
      return path;
    }
  },
  //end of methods

  computed: {
    //
    radius: function() {
      var that = this;
      var radius = Math.min(that.cfg.w / 2, that.cfg.h / 2);
      return radius;
    },
    rScale: function() {
      var that = this;
      var rScale = d3
        .scaleLinear()
        .range([0, that.radius])
        .domain([0, that.maxValue]);
      return rScale;
    },
    color_b1: function() {
      var color_b1 = d3
        .scaleOrdinal()
        .domain([
          "corruption",
          "war_in_donbas",
          "utilities_cost_quality",
          "healthcare_cost_quality",
          "high_prices_low_salaries"
        ])
        .range(["#7c0055", "#0076a3", "#6a8a22", "#d3a809", "#726963"]);
      return color_b1;
    },
    color_b2: function() {
      var color = d3
        .scaleOrdinal()
        .domain(["anticor_resp", "cor_spread", "anticor_will"])
        .range([this.blue, this.red, this.green]);
      return color;
    },
    color_b3: function() {
      var color = d3
        .scaleOrdinal()
        .domain(["dealt_with_sphere", "corruption_spread", "faced_corruption"])
        .range([this.blue, this.red, this.green]);
      return color;
    },
    color_b4_pro: function() {
      var color = d3
        .scaleOrdinal()
        .domain([
          ",          ?",
          "      ,    ?",
          "  ,     ?"
        ])
        .range(["#88ac2e", "#0092c8", "#ffcb05"]);
      return color;
    },
    color_b4_contra: function() {
      var color = d3
        .scaleOrdinal()
        .domain([
          " ,     ?",
          "      ?",
          "   ,    ?"
        ])
        .range(["#9a0064", "#ca6c18", "#5a2a00"]);
      return color;
    },
    color_alternative_b2: function() {
      var color = d3
        .scaleOrdinal()
        .domain(["anticor_resp", "cor_spread", "anticor_will"])
        .range(["blue", "red", "green"]);
      return color;
    },
    scale() {
      var y = d3
        .scaleLinear()
        .range([lineChartHeight, 0])
        .domain([0, 1.1]);

      var x = d3
        .scaleLinear()
        .range([0, lineChartWidth])
        .domain([2006, 2021]);
      return { x, y };
    },

    //  
    d3LinePath: function() {
      var that = this;
      const path = d3
        .line()
        .x(d => that.scale.x(+d.year))
        .y(d => that.scale.y(+d.value));
      return path;
    },

    d3AreaPath: function() {
      var that = this;
      const path = d3
        .area()
        .x(d => that.scale.x(+d.year))
        .y0(lineChartHeight)
        .y1(d => that.scale.y(+d.value));
      return path;
    },

    /* --------------------------------------------------------------------------------------------------------------------
   ---------------------------------------------------------- 1 -----------------------------------------------------
   --------------------------------------------------------------------------------------------------------------------- */
    b1_general: function() {
      var that = this;
      var b1_general = block1.filter(function(d) {
        return d.sample === that.b1_data_sample;
      });
      return b1_general;
    },
    b1_radar_Ukraine: function() {
      var that = this;
      var radar_Ukraine = [];
      var radar_Ukraine_pre = that.b1_general.filter(function(d) {
        return d.year === that.actual_year && d.obl === "Ukraine";
      });
      radar_Ukraine.push(radar_Ukraine_pre);
      radar_Ukraine.sort(function(a, b) {
        return d3.ascending(a.axis, b.axis);
      });
      return radar_Ukraine;
    },
    b1_radar_region: function() {
      var that = this;
      var radar_region = [];
      var radar_region_pre = that.b1_general.filter(function(d) {
        return d.year === that.actual_year && d.obl === that.region;
      });
      radar_region.push(radar_region_pre);
      radar_region.sort(function(a, b) {
        return d3.ascending(a.axis, b.axis);
      });
      return radar_region;
    },
    b1_map1_data: function() {
      var that = this;
      var map1_data = that.b1_general.filter(function(d) {
        return d.obl != "Ukraine" && d.year === that.actual_year && d.axis === "corruption";
      });

      map1_data.map(function(d) {
        if (d.value > 0) {
          d.percent = " " + Math.floor(d.value * 100) + "%";
        } else {
          d.percent = "  ";
        }
        d.ukrName = ukr_names[d.obl];
      });
      return map1_data;
    },
    b1_map1_rect: function() {
      var that = this;
      var map_rect = [];
      that.b1_map1_data.forEach(function(d) {
        if (!map_rect.includes(d.opacity)) {
          map_rect.push(d.opacity);
        }
      });
      map_rect.sort(function(a, b) {
        return (a === null) - (b === null) || +(a > b) || -(a < b);
      });
      return map_rect;
    },
    b1_line_data: function() {
      var that = this;
      var data = that.b1_general.filter(function(d) {
        if (d.value != 0) {
          return d.obl === "Ukraine" && d.axis === "corruption";
        }
      });

      var lastYear = data.filter(function(d) {
        return d.year === that.actual_year;
      });
      var max = d3.max(data, function(d) {
        return d.value;
      });
      var min = d3.min(data, function(d) {
        return d.value;
      });

      if (max - min < 0.1) {
        max = d3.max(lastYear, function(d) {
          return d.value;
        });
        min = d3.min(lastYear, function(d) {
          return d.value;
        });
      }

      data.map(function(d) {
        d.x = that.scale.x(d.year) - 10;
        d.y = that.scale.y(d.value) - 10;
      });

      data = data.sort(function(a, b) {
        return a.year - b.year;
      });

      return { data, max, min };
    },

